import React, { Component } from 'react';
import '../../font/iconfont.css';
import './Game.scss';
import {withRouter} from "react-router-dom";

class Game extends Component {
	
	constructor(props){
		super(props);
		this.state = {
			url: this.getUrl(props.match.params.cid),
			style: this.getStyle()
		}
	}

	componentDidMount(){
		var event = "click";
		if(navigator.userAgent.match(/(iPhone|iPod|Android|ios)/i)){
			event = "touchstart";
		}

		this.refs.btn.addEventListener(event, (e)=> {
			e.preventDefault();
			e.stopImmediatePropagation();
			this.props.history.goBack();
		}, false);

	}

	getStyle(){

		var w = window.innerWidth;
		var h = window.innerHeight;
		var gw = w;
		var gh = h;
		if(w / h > 0.76){
			gw = h * 0.76;
		}
		return {
			width: gw + "px",
			height: gh + "px"
		}

	}

	
	getUrl(n){
		n = Number(n);
		if(n === 0){
			return "http://192.168.2.103:8080/game/point/index.html";
		}
		else if(n === 1){
			return "http://192.168.2.103:8080/game/puzzle/index.html";
		}
		else if(n === 2){
			return "http://jsz315.github.io/draw/";
		}
		else if (n === 3) {
			return "http://192.168.2.103:8080/game/catch/index.html";
		}
		else if (n === 4) {
			return "http://jsz315.github.io/vue-data-picker/#/test";
		}
		else if(n === 5){
			return "http://jsz315.github.io/three/";
		}
		return "";
	}

	back(event){
		event.nativeEvent.stopImmediatePropagation();
		//event.nativeEvent.preventDefault();
		console.log(event.nativeEvent);
		this.props.history.goBack();
	}

  	render() {
	    return (
	      <div className="Game">
	      	<i className="iconfont icon-guanbi" ref="btn"></i>
	      	<iframe className="iframe" src={this.state.url} style={this.state.style} title="Game"></iframe>
	      </div>
    	);
  	}
}

export default withRouter(Game);



