import React, { Component } from 'react';
import './User.scss';
import '../../font/iconfont.css';
import Code from '../code/code';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      showQRCode: false
    }
  }
  show(){
    this.setState({showQRCode: true});
  }
  hide(){
    this.setState({showQRCode: false});
  }



  render() {
    return (
      <div className="user">
  
        <i className="header iconfont icon-ren"></i>

        <div className="info">
          <div className="name"></div>
          <div className="email">
            <i className="iconfont icon-youxiang"></i>
            <span className="info-txt">11025373@qq.com</span>
          </div>
          <div className="tel">
            <i className="iconfont icon-mobile"></i>
            <span className="info-txt">17682344707</span>
          </div>
          
        </div>
        <i className="header iconfont icon-erweima" onClick={this.show.bind(this)}></i>
        {this.state.showQRCode && <Code isLink={true} hideCode={this.hide.bind(this)}/>}
      </div>
    );
  }
}

export default App;



