import React, { Component } from 'react';
import './Work.scss';
import '../../font/iconfont.css';
import {withRouter} from "react-router-dom";
import Data from '../../common/data.js';
import Image0 from './poster/0.jpg';
import Image1 from './poster/1.jpg';
import Image2 from './poster/2.jpg';
import Image3 from './poster/3.jpg';
import Image4 from './poster/4.jpg';
import Image5 from './poster/5.jpg';


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      list: [],
      pics: [
        Image0,Image1,Image2,Image3,Image4,Image5
      ]
    };
  }
  jump(n){
    this.props.history.push("/game/" + n);
  }
  componentDidMount(){
    this.setState({list: Data});
  }
  render() {
    return (
      <div className="work">

        <div className="title">
          <i className="iconfont icon-zuopin"></i>
        </div>

        <div className="list">

        {
          this.state.list.map((item, index)=>{
            return (
              <div className="item" onClick={this.jump.bind(this, index)} key={index}>
                <div className="item-pic" style={{'backgroundImage': `url(${this.state.pics[index]})`}}>
                  
                </div>
                <div className="item-name">{item.name}</div>
                <div className="tags">
                {
                  item.tags.map((tag, id)=><span className="tag" key={id}>{tag}</span>)
                }
                </div>
                <div className="item-txt">{item.desc}</div>

              </div>
              )
          })
        }

       </div>
      </div>
    );
  }
}

export default withRouter(App);


