import React, { Component } from 'react';
import './Money.scss';
import '../../font/iconfont.css';
import Code from '../code/code';
import Image0 from './zhifubao.jpg';
import Image1 from './weixin.jpg';


class Money extends Component {
  constructor(props){
    super(props);
    this.state = {
      showQRCode: false,
      src: ""
    }
  }
  pay(n){
    var link = "";
    if(n === 1){
      link = "https://payapp.weixin.qq.com/aa/detail?collection_no=cb643d396b8fac4f83acda47aae43cb95104bb291f4e0581fc5ade1dd7f83848f0f709cb589c7ac55ad11d5ef31e2b41&showwxpaytitle=1&ch=qr";
    }
    else{
      link = "https://szsupport.weixin.qq.com/cgi-bin/mmsupport-bin/readtemplate?t=w_redirect_taobao&url=https%3A%2F%2Fqr.alipay.com%2Ffkx08410so7jltyqzn4bdf6&lang=zh_CN";
    }
    window.location = link;
  }
  show(n){
    var temp = this.state;
    if(n === 0){
      temp.src = Image0;
    }
    else{
      temp.src = Image1;
    }
    temp.showQRCode = true;
    this.setState(temp);
  }
  hide(){
    var temp = this.state;
    temp.showQRCode = false;
    this.setState(temp);
  }
  render() {
    return (
      <div className="money">
        <div className="zfb" onClick={this.show.bind(this, 0)}>
          <i className="iconfont icon-zhifubao"></i>
        </div>
        <div className="weixin" onClick={this.show.bind(this, 1)}>
          <i className="iconfont icon-weixin"></i>
        </div>

        <div className="tip"><span className="big"></span></div>
        {this.state.showQRCode && <Code isLink={false} src={this.state.src} hideCode={this.hide.bind(this)}/>}
        
      </div>
    );
  }
}

export default Money;


