import React, { Component } from 'react';
import './Code.scss';
import QRCode from 'qrcode.react';

class Code extends Component {

  render() {
    return (
      <div className="code" onClick={this.props.hideCode.bind(this)}>
        {this.props.isLink ? 
        	<QRCode value={this.props.link} className="img"/>
        	:
        	<div className="img"><img src={this.props.src} /></div>
        }
      </div>
    );
  }
}


Code.defaultProps = {
  link: window.location.href,
  isLink: false,
  src: ""
}

Code.propTypes = {

}

export default Code;



